    width: 30px;
    @each $name, $color in $marker-colors {
      &.marker-#{$name} {
        background-color: #{$color};
        border-color: #{$color};
      }
    }
  }
	&__results {
		display: flex;
		flex-direction: column;
		gap: 8px;
		grid-area: results;

		&--loading {
			display: flex;
			justify-content: center;
			min-height: 30dvh;
			place-items: center;
			svg {
				max-width: 3rem;
			}
		}
	}
